<template>
  <div id="app-wrapper" class="m-plus-1-400">
    <header>
      <a href="./"><h1>レスレリ所持キャラチェッカー</h1></a>
      <nav>
        <a href="https://pomucat.github.io/"><i class="bi bi-house-door-fill"></i></a>
      </nav>
    </header>
    <main>
      <b-card v-if="isImportMode" class="importmode p-3">
          <p>※現在は閲覧モードです。所持キャラリストを上書きしますか？</p>
          <b-button @click="importOwnedStatus" variant="secondary">上書き保存する</b-button>&nbsp;
          <b-button @click="closeImportMode" variant="secondary">保存せずに戻る</b-button>
      </b-card>
      <b-container>
        <div>
          <b-row class="mt-2 mb-2">
            <b-col>
              <b-button-group class="m-1">
                <b-button @click="sortCharactersBy('id', true)" :variant="currentSort==='id' ? 'primary' : 'secondary'">実装順</b-button>
                <b-button @click="sortCharactersBy('name', false)" :variant="currentSort==='name' ? 'primary' : 'secondary'">名前順</b-button>
                <b-button @click="sortCharactersBy('rarity', true)" :variant="currentSort==='rarity'||currentSort==='currentRarity' ? 'primary' : 'secondary'">レアリティ順</b-button>
              </b-button-group>
              
              <!-- rarityフィルタ -->
              <b-button-group class="m-1">
                <b-form-select
                  v-model="rarityMode"
                  :options="rarityOptions"
                  class="rarity-select"
                ></b-form-select>
                <b-button 
        v-for="rarity in ['1', '2', '3', '3.5', '4', '4.5', '5']"
        :key="rarity"
        :variant="selectedRarities.includes(rarity) ? 'primary' : 'secondary'"
        :disabled="rarityMode === 'rarity' && !['1', '2', '3'].includes(rarity)" 
        @click="toggleRarityFilter(rarity)">
        {{ rarity }}
        </b-button>
              </b-button-group>
              
              <!-- roleフィルタボタン -->
              <b-button-group class="m-1">
                <b-button 
                  v-for="role in ['a', 'b', 'd', 's']" 
                  :key="role"
                  :variant="selectedRoles.includes(role) ? 'primary' : 'secondary'"
                  @click="toggleRoleFilter(role)"
                  class="icon-button">
                  <b-img :src="require(`@/assets/icon/${role}.png`)" fluid />
                </b-button>
              </b-button-group>
              
              <!-- elementフィルタボタン -->
              <b-button-group class="m-1">
                <b-button 
                  v-for="element in ['火', '氷', '雷', '風', '斬', '打', '突']" 
                  :key="element"
                  :variant="selectedElements.includes(element) ? 'primary' : 'secondary'"
                  @click="toggleElementFilter(element)"
                  class="icon-button">
                  <b-img :src="require(`@/assets/icon/${element}.png`)" fluid />
                </b-button>
              </b-button-group>
                  
              <!-- giftフィルタボタン -->
              <b-button-group class="m-1">
                <b-button 
                  v-for="giftL in ['b', 'p', 'y', 'r', 'g']" 
                  :key="giftL"
                  :variant="selectedGiftL.includes(giftL) ? 'primary' : 'secondary'"
                  @click="toggleGiftLFilter(giftL)"
                  class="icon-button">
                  <i class="bi bi-caret-left-fill gift-icon" :class="getGiftColor(giftL)"></i>                </b-button>
              </b-button-group>

              <b-button-group class="m-1">
                <b-button 
                  v-for="giftR in ['b', 'p', 'y', 'r', 'g']" 
                  :key="giftR"
                  :variant="selectedGiftR.includes(giftR) ? 'primary' : 'secondary'"
                  @click="toggleGiftRFilter(giftR)"
                  class="icon-button">
                  <i class="bi bi-caret-right-fill gift-icon" :class="getGiftColor(giftR)"></i>                </b-button>
              </b-button-group>

              <b-button-group class="m-1">
                <b-form-select v-model="labelMode" :options="labelOptions" class="label-select"></b-form-select>
              </b-button-group>

              <b-button-group class="m-1">                
                <b-button @click="filterOwned" :variant="showOwnedOnly ? 'primary' : 'secondary'">所持キャラ</b-button>
                <b-button @click="decreaseIconCount" variant='primary'><i class="bi bi-zoom-in"></i></b-button>
                <b-button @click="increaseIconCount" variant='primary'><i class="bi bi-zoom-out"></i></b-button>
              </b-button-group>
            
            </b-col>
          </b-row>
        </div>
      </b-container>
      <b-container class="mb-5">
        <div  ref="container">
          <b-row>
            <b-tabs class="custom-tabs mt-3 mb-3" v-model="currentTab">
            <b-tab title="一覧表示">
              <b-row class="capture-all">
                <b-col>
                  <div class="iconbox">
                    <CharacterIcon 
                      v-for="character in filteredCharacters" 
                      :key="character.id" 
                      :size="iconSize" 
                      :character="character" 
                      :rarity-mode="rarityMode"
                      :label-mode="labelMode"
                      :is-import-mode="isImportMode"
                      @update-character="updateCharacter" />
                  </div>
                </b-col>
              </b-row>
              
            </b-tab>
            <b-tab title="テーブル表示">
              <b-row  class="capture-table">
                <b-col>
                  <b-container class="table-view">
                    <div  v-for="(element) in ['火', '氷', '雷', '風', '斬', '打', '突']" :key="element" class="table-row">
                      <div  v-for="(role) in ['a', 'b', 'd', 's']" :key="role" class="table-cell">
                        <CharacterIcon 
                      v-for="character in getIconsForCell(role, element)" 
                      :key="character.id" 
                      :size="iconSize" 
                      :character="character" 
                      :rarity-mode="rarityMode"
                      :label-mode="labelMode"
                      :is-import-mode="isImportMode"
                      @update-character="updateCharacter" />
                      </div>
                    </div>
                  </b-container>
                </b-col>
              </b-row>
            </b-tab>
            <b-tab title="リスト表示">
              <CharacterList 
              v-for="character in filteredCharacters"
              :key="character.id" 
              :character="character" 
              :rarity-mode="rarityMode"
              :is-import-mode="isImportMode"
              @update-character="updateCharacter"
              />
            </b-tab>
          </b-tabs>
          </b-row>
        </div>
        
        <div class="input-group mt-3 mb-3">
          <span class="input-group-text" ><i class="bi bi-share"></i></span>
          <input  class="form-control" :value="shareableUrl"  onfocus="this.select();" readonly />
            
          <b-button :href="`https://twitter.com/intent/tweet?text=%E3%82%B9%E3%83%9E%E3%83%9BRPG%E3%81%AF%E4%BB%8A%E3%81%93%E3%82%8C%E3%82%92%E3%82%84%E3%81%A3%E3%81%A6%E3%82%8B%E3%82%88%E3%80%82%E4%BB%8A%E3%81%AE%E6%89%80%E6%8C%81%E3%82%AD%E3%83%A3%E3%83%A9%E3%81%AF%E3%81%93%E3%81%A1%E3%82%89%EF%BC%81+%7c+%E3%83%AC%E3%82%B9%E3%83%AC%E3%83%AA%E6%89%80%E6%8C%81%E3%82%AD%E3%83%A3%E3%83%A9%E3%83%81%E3%82%A7%E3%83%83%E3%82%AB%E3%83%BC+%23%E3%83%AC%E3%82%B9%E3%83%AC%E3%83%AA+${encodeURIComponent(shareableUrl)}`" variant='secondary' target="_blank">
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="#1DA1F2">
                <path d="M23.954 4.569c-.885.389-1.83.654-2.825.775 1.014-.611 1.794-1.574 2.163-2.723-.951.564-2.005.974-3.127 1.195-.896-.959-2.173-1.559-3.594-1.559-2.723 0-4.928 2.204-4.928 4.928 0 .386.045.762.127 1.124-4.09-.205-7.719-2.164-10.148-5.144-.423.722-.666 1.561-.666 2.457 0 1.696.865 3.189 2.181 4.068-.803-.026-1.56-.246-2.22-.616v.061c0 2.367 1.684 4.342 3.918 4.789-.41.111-.842.171-1.287.171-.314 0-.619-.03-.916-.086.631 1.956 2.445 3.379 4.6 3.419-1.685 1.319-3.809 2.106-6.114 2.106-.397 0-.788-.023-1.175-.068 2.179 1.397 4.768 2.212 7.548 2.212 9.058 0 14.01-7.507 14.01-14.01 0-.213-.004-.426-.014-.637.961-.695 1.796-1.562 2.457-2.548l-.047-.02z"/>
            </svg>
            Tweet
          </b-button>
          <b-button @click="shareLink" variant="secondary">
            <i class="bi bi-paperclip"></i>&nbsp;コピー
          </b-button>
          <CustomToast ref="toast" message="リンクがクリップボードに保存されました！" />
        </div>
      </b-container>
    </main>
    <footer>
      <span>画像は著作権法第32条第1項に基づき コーエーテクモゲームス 様より引用しています。</span><br>
      <span>&copy; 2024 pomucat.</span>
    </footer>
  </div> 
</template>

<script>
import CharacterIcon from './components/CharacterIcon.vue';
import CharacterList from './components/CharacterList.vue';
import characters from './characters';
import CustomToast from './components/CustomToast.vue';

export default {
  components: {
    CharacterIcon,
    CharacterList,
    CustomToast
  },
  data() {
    return {
      characters: characters,
      showOwnedOnly: false,
      currentSort: "id",
      sortOrder: 'asc',
      selectedRarities: [],
      selectedRoles: [],
      selectedElements: [],
      selectedGiftL: [],
      selectedGiftR: [],
      labelMode: 'none',
      labelOptions: [
        { value: 'name', text: '名前' },
        { value: 'rarity', text: 'レアリティ' },
        { value: 'none', text: '非表示' }
      ],
      rarityMode: 'rarity',
      rarityOptions: [
        { value: 'rarity', text: '初期★' },
        { value: 'currentRarity', text: '現在★' }
      ],
      rarityValues: ['1', '2', '3'],  // デフォルトは初期レアリティの値
      iconCount: 8,
      minIcons: 4,
      maxIcons: 16,
      containerWidth: 0,
      currentTab: 0,
      importedOwnedStatus: null,
      isImportMode: false,
      shareable: false,
    };
  },
  
  computed: {
    shareableUrl() {
      const encodedData = this.encodeOwnedStatusToBase64();
      const encodedRarity = this.encodeCurrentRarityToBase64();
      return `${window.location.origin}${window.location.pathname}?d=${encodedData}&r=${encodedRarity}`;
    },
    iconSize() {
      const numIconsPerRow = Math.ceil(this.iconCount);
      return `${Math.floor(this.containerWidth / numIconsPerRow)}px`;
    },
    filteredCharacters() {
      let filtered = this.characters;

      if (this.showOwnedOnly) {
        filtered = filtered.filter(character => character.owned);
      }

      if (this.selectedRarities.length > 0) {
        if(this.rarityMode === 'rarity'){
          filtered = filtered.filter(character => this.selectedRarities.includes(character.rarity));
        }else{
          filtered = filtered.filter(character => this.selectedRarities.includes(character.currentRarity));
        }
      }

      if (this.selectedRoles.length > 0) {
        filtered = filtered.filter(character => this.selectedRoles.includes(character.role));
      }

      if (this.selectedElements.length > 0) {
        filtered = filtered.filter(character => this.selectedElements.includes(character.element));
      }
      if (this.selectedGiftL.length > 0) {
        filtered = filtered.filter(character => this.selectedGiftL.includes(character.giftL));
      }
      if (this.selectedGiftR.length > 0) {
        filtered = filtered.filter(character => this.selectedGiftR.includes(character.giftR));
      }

      return filtered;
    },
  },
  watch: {
    rarityMode(newMode) {
      if (newMode === 'rarity') {
        this.rarityValues = ['1', '2', '3'];
      } else if (newMode === 'currentRarity') {
        this.rarityValues = ['1', '2', '3','3.5', '4', '4.5', '5'];
      }
      this.saveSettings();
    },
    labelMode() {
      this.saveSettings();
    },
  },
  methods: {
    getGiftColor(gift) {
        switch (gift) {
          case 'p': return 'text-purple';
          case 'g': return 'text-green';
          case 'r': return 'text-red';
          case 'y': return 'text-yellow';
          case 'b': return 'text-blue';
          default: return '';
        }
      },
      updatePopstate() {
        this.shareable = !!navigator.share;
        // クエリが存在するかを判定して、データプロパティを更新
        const params = new URLSearchParams(window.location.search);
        this.isImportMode = params.has('d');
        
        if (this.isImportMode) {
          const encodedData = params.get('d');
          this.importedOwnedStatus = this.decodeBase64ToOwnedStatus(encodedData);

          // レアリティ情報があるかどうかチェック
          let importCurrentRarity = [];
          if (params.has('r')) {
            const encodedRarity = params.get('r');
            importCurrentRarity = this.decodeBase64ToCurrentRarity(encodedRarity);
          }

          // クエリから復元するキャラクターの所持状況とレアリティをマージして取得
          this.characters = this.characters.map(originalCharacter => {
            const importedCharacter = this.importedOwnedStatus.find(char => char.id === originalCharacter.id);
            const rarityData = importCurrentRarity.find(char => char.id === originalCharacter.id);
            return importedCharacter
              ? {
                  ...originalCharacter, // 既存のプロパティを保持
                  owned: importedCharacter.owned,
                  currentRarity: rarityData ? rarityData.currentRarity : originalCharacter.rarity
                }
              : originalCharacter; // 一致するキャラクターがない場合はそのまま
          });
        } else {
          // 所持状況とレアリティの保存データが存在するかを確認
          const savedStates = localStorage.getItem('characterOwnedStates');
          
          if (savedStates) {
            const parsedStates = JSON.parse(savedStates);
            
            // 保存されたデータを復元する際、元のプロパティを保持
            this.characters = this.characters.map(originalCharacter => {
              const savedCharacter = parsedStates.find(char => char.id === originalCharacter.id);
              return savedCharacter
                ? {
                    ...originalCharacter, // 既存のプロパティを保持
                    owned: savedCharacter.owned,
                    currentRarity: savedCharacter.currentRarity || originalCharacter.rarity
                  }
                : originalCharacter; // 一致するキャラクターがない場合はそのまま
            });
          }
        }
      },
    shareLink() {
        navigator.clipboard.writeText(this.shareableUrl).then(() => {
        this.$refs.toast.showToast(); // クリップボードにコピー後にトーストを表示
      });
    },
    base64urlToBinary(base64urlString) {
      const base64urlMap = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-_';

      const binaryString = base64urlString
        .split('')
        .map(char => {
          const index = base64urlMap.indexOf(char);
          if (index === -1) {
            return '000000'; // 無効な文字は 'A' のバイナリ "000000" として処理
          }
          return index.toString(2).padStart(6, '0'); // 6ビットに変換
        })
        .join('');

      return binaryString;
    },
    binaryToBase64url(binaryString) {
      const base64urlMap = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-_';

      // 6桁ごとに区切り、足りない桁には末尾に0を埋める
      const paddedBinaryString = binaryString.padEnd(Math.ceil(binaryString.length / 6) * 6, '0');

      // 6桁ずつ取り出してBase64urlに変換
      const base64urlString = paddedBinaryString
        .match(/.{1,6}/g) // 6桁ごとに区切る
        .map(binary => {
          const index = parseInt(binary, 2); // 2進数から10進数に変換
          return base64urlMap[index]; // Base64url文字列に変換
        })
        .join('');

      return base64urlString;
    },
    sortCharactersById() {
      return [...this.characters].sort((a, b) => parseInt(a.id) - parseInt(b.id));
    },

    decodeBase64ToOwnedStatus(encodedData) {
      const sortedCharacters = this.sortCharactersById();
      const binaryString = this.base64urlToBinary(encodedData);

      // charactersに対応させてowned状態を更新
      return sortedCharacters.map((character, index) => ({
        ...character,
        owned: binaryString[index] === '1' ? true : false
      }));
    },

    decodeBase64ToCurrentRarity(encodedData) {
      const sortedCharacters = this.sortCharactersById();
      const rarityMap = {
        '001': '1', '010': '2', '011': '3', '100': '3.5',
        '101': '4', '110': '4.5', '111': '5'
      };

      const binaryString = this.base64urlToBinary(encodedData);
      return sortedCharacters.map((character, index) => {
        const binaryValue = binaryString.substring(index * 3, index * 3 + 3) || '000'; 
        const currentRarity = rarityMap[binaryValue] || character.rarity;
        return {
          ...character,
          currentRarity
        };
      });
    },

    encodeOwnedStatusToBase64() {
      const sortedCharacters = this.sortCharactersById();
      const binaryString = sortedCharacters.map(character => 
        character.owned ? "1" : "0"
      ).join("");
      const base64url = this.binaryToBase64url(binaryString);
      return base64url;
    },

    encodeCurrentRarityToBase64() {
      const sortedCharacters = this.sortCharactersById();
      const rarityMap = {
        '1': '001', '2': '010', '3': '011', '3.5': '100',
        '4': '101', '4.5': '110', '5': '111'
      };

      const binaryString = sortedCharacters.map(character => {
        const rarity = character.currentRarity || character.rarity;
        return rarityMap[rarity] || '000';
      }).join('');
      const base64url = this.binaryToBase64url(binaryString);
      return base64url;
    },

    closeImportMode() {
      this.importedOwnedStatus = null;

      this.saveSettings();
      const url = window.location.origin + window.location.pathname;
      window.history.pushState(null, '', url);
      const savedStates = localStorage.getItem('characterOwnedStates');
      if(savedStates){
        this.applySavedOwnedStates(JSON.parse(savedStates));
      } else {
        this.characters = characters;
      }
      this.updatePopstate();
    },

    importOwnedStatus() {
      const ownedStates = this.characters.map(character => ({ 
        id: character.id, 
        owned: character.owned, 
        currentRarity: character.currentRarity 
      }));
      localStorage.setItem('characterOwnedStates', JSON.stringify(ownedStates));
      this.closeImportMode();
    },


    getIconsForCell(role, element) {
      return this.filteredCharacters
        .filter(character => character.role === role && character.element === element);
    },
    decreaseIconCount() {
      if (this.iconCount > this.minIcons) {
        this.iconCount -= 4;
        this.updateContainerWidth();
        this.saveSettings();
      }
    },
    increaseIconCount() {
      if (this.iconCount < this.maxIcons) {
        this.iconCount += 4;
        this.updateContainerWidth();
        this.saveSettings();
      }
    },
    updateContainerWidth() {
      this.$nextTick(() => {
        const container = this.$refs.container;
        if (container) {
          this.containerWidth = container.clientWidth;
        } else {
          console.warn('Container ref not found.');
        }
      });
    },
    toggleRarityFilter(rarity) {
      const index = this.selectedRarities.indexOf(rarity);
      if (index === -1) {
        this.selectedRarities.push(rarity);
      } else {
        this.selectedRarities.splice(index, 1);
      }
      this.saveSettings();
    },
    toggleRoleFilter(role) {
      const index = this.selectedRoles.indexOf(role);
      if (index === -1) {
        this.selectedRoles.push(role);
      } else {
        this.selectedRoles.splice(index, 1);
      }
      this.saveSettings();
    },
    toggleElementFilter(element) {
      const index = this.selectedElements.indexOf(element);
      if (index === -1) {
        this.selectedElements.push(element);
      } else {
        this.selectedElements.splice(index, 1);
      }
      this.saveSettings();
    },
    toggleGiftLFilter(element) {
      const index = this.selectedGiftL.indexOf(element);
      if (index === -1) {
        this.selectedGiftL.push(element);
      } else {
        this.selectedGiftL.splice(index, 1);
      }
      this.saveSettings();
    },
    toggleGiftRFilter(element) {
      const index = this.selectedGiftR.indexOf(element);
      if (index === -1) {
        this.selectedGiftR.push(element);
      } else {
        this.selectedGiftR.splice(index, 1);
      }
      this.saveSettings();
    },
    sortCharactersBy(property, isNumeric = false) {
      let option = property;
      if(property==='rarity'&&this.rarityMode==='currentRarity'){
        option = 'currentRarity';
      }

      if (this.currentSort === option) {
        this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
      } else {
        this.currentSort = option;
        this.sortOrder = 'asc';
      }

      this.characters.sort((a, b) => {
        const valueA = a[option];
        const valueB = b[option];


        let comparison;
        if (isNumeric) {
          comparison = valueA - valueB;
        } else {
          comparison = valueA.localeCompare(valueB);
        }

        comparison = this.sortOrder === 'asc' ? comparison : -comparison;

        if (comparison === 0 && option !== 'id') {
          comparison = a.id - b.id;
        }
        return comparison;
      });
      

      this.saveSettings();
    },
    filterOwned() {
      this.showOwnedOnly = !this.showOwnedOnly;
      this.saveSettings();
    },
    updateCharacter(updatedCharacter) {
      const index = this.characters.findIndex(character => character.id === updatedCharacter.id);
      if (index !== -1) {
        this.characters[index] = updatedCharacter;
      }
      if(!this.isImportMode){
        const ownedStates = this.characters.map(character => ({ 
          id: character.id, 
          owned: character.owned, 
          currentRarity: character.currentRarity 
        }));
        localStorage.setItem('characterOwnedStates', JSON.stringify(ownedStates));
      }
    },
    applySavedOwnedStates(savedStates) {
      savedStates.forEach(savedState => {
        const character = this.characters.find(character => character.id === savedState.id);
        if (character) {
          character.owned = savedState.owned;
        }
      });
    },
    saveSettings() {
    const settings = {
      iconCount: this.iconCount,
      selectedRarities: this.selectedRarities,
      selectedRoles: this.selectedRoles,
      selectedElements: this.selectedElements,
      currentSort: this.currentSort,
      sortOrder: this.sortOrder,
      selectedGiftL: this.selectedGiftL,
      selectedGiftR: this.selectedGiftR,
      labelMode: this.labelMode,
      rarityMode: this.rarityMode,
      };
      localStorage.setItem('characterSettings', JSON.stringify(settings));
    },
    
  },
  created() {
    this.updatePopstate();
  },
  mounted() {
    window.addEventListener('popstate', this.updatePopstate);

    this.updateContainerWidth();
    window.addEventListener('resize', this.updateContainerWidth);

    const savedSettings = localStorage.getItem('characterSettings');
    if (savedSettings) {
      const settings = JSON.parse(savedSettings);
      this.iconCount = settings.iconCount || 8;
      this.selectedRarities = settings.selectedRarities || [];
      this.selectedRoles = settings.selectedRoles || [];
      this.selectedElements = settings.selectedElements || [];
      this.selectedGiftL = settings.selectedGiftL || [];
      this.selectedGiftR =  settings.selectedGiftR || [];
      this.labelMode = settings.labelMode || 'none';
      this.rarityMode = settings.rarityMode || 'rarity';
      this.currentSort = settings.currentSort || 'id';
      this.sortOrder = settings.sortOrder || 'asc';
      this.sortOrder = (this.sortOrder==='asc') ? 'desc' :'asc'; // 反転して入れておく
      this.sortCharactersBy(this.currentSort,true); // asc descが反転する。
    }
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.updateContainerWidth);
    window.removeEventListener('popstate', this.updatePopstate);
  }
};
</script>

