<template>
  <div class="fixed-size text-center" @click="toggleOwned" @touchend="removeHoverEffect" :style="{ width: size, height: size }" :class="{ 'not-owned': !character.owned }">
    <b-img :src="imageSrc" alt="character-icon" class="character-image" fluid></b-img>
    <div class="role-label"><b-img :src="require(`@/assets/icon/${character.role}.png`)" fluid /></div>
    <div class="element-label"><b-img :src="require(`@/assets/icon/${character.element}.png`)" fluid /></div>
    <div v-if="labelMode==='name'" class="name-label">{{ character.name }}【{{character.title}}】</div>
    <div v-else-if="labelMode==='rarity'"  class="rarity-label">
      <img v-for="(star, index) in generateStars()" 
           :key="index" 
           :src="require(`@/assets/icon/${star}`)" 
           alt="Star"
           class="star-icon">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    character: Object,
    size: String,
    labelMode: String,
    rarityMode: String,
    isImportMode: Boolean 
  },
  computed: {
    imageSrc() {
      const imageName = this.character.id.toString().padStart(3, '0') + '.jpg';
      return require(`@/assets/character/${imageName}`) || require('@/assets/character/000.jpg');
    }
  },
  methods: {
    generateStars() {
    const  rarity = this.character[this.rarityMode];
    const stars = [];
    for (let i = 1; i <= 5; i++) {
      if (rarity >= i) {
        stars.push('star_fill.webp'); // 星が満たされている場合
      } else if (rarity > i - 1 && rarity < i) {
        stars.push('star_half.webp'); // 星が半分の場合
      } else {
        stars.push('star.webp'); // 星が満たされていない場合
      }
    }
    return stars;
  },
    toggleOwned() {
      if(!this.isImportMode){
        this.$emit('update-character', { ...this.character, owned: !this.character.owned });
      }
    },
    removeHoverEffect() {
      const nameLabel = this.$el.querySelector('.name-label');
      if (nameLabel) {
        nameLabel.classList.remove('hover');
      }
    }
  }
};
</script>

<style scoped>
.fixed-size {
  position: relative;
  padding: 0;
  margin: 0;
  overflow: hidden;
  cursor: pointer;

}
.fixed-size:hover {
  background-color: rgb(169, 169, 172);
  transition-duration: 0.2s;
  transition-property: all;
  transition-timing-function: ease-in;
}
.character-image {
  display: block;
  border-radius: 50%; 
  overflow: hidden;
  width: 100%;
}
.not-owned{
 filter: grayscale(100%) brightness(50%);
}

.element-label {
  position: absolute;
  top: 0;
  right: 0;
  width: 30%;
}
.role-label {
  position: absolute;
  top: 0;
  left: 0;
  width: 30%;
}
.element-label > img,
 .role-label > img{
  display: block;
  position: relative;
}


.name-label {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* 半透明の背景色 */
  color: white;
  text-align: center;
  padding: 2px 0;
  font-size: 0.8rem;
  
  user-select: none;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis; 
  transition: all 0.3s ease; 
}

.fixed-size:hover .name-label,
.fixed-size .name-label.hover {
  white-space: normal; 
  overflow: visible; 
  text-overflow: clip; 
  background-color: rgba(0, 0, 0, 0.8); 
  padding: 2px 0px;
}

.rarity-label {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: rgb(239, 230, 156);
  background-color: rgba(0, 0, 0, 0.5); /* 半透明の背景色 */
  text-align: center;
  height: 20%;
  display: flex; /* アイコンを横並びにする */
  justify-content: center; /* アイコンを中央揃えにする */
  align-items: center; /* 縦方向の揃え */
  white-space: nowrap; /* 改行を防ぐ */
  padding: 0 5px;
}
.star-icon {
  width: 20%;
}
</style>
