<template>
    <b-container class="character-item">
        <b-col class="col1" @click="toggleOwned">
            <img
                :src="getCharacterIcon(character.id)"
                :class="{ 'grayscale': !character.owned }"
                alt="Character Icon"
                class="character-icon"
                
            />
            <div class="character-name">{{ character.name }}</div>
            <div class="character-title">【{{ character.title }}】</div>
            
        </b-col>
        <b-col class="col2">
            
            <img v-for="(star, index) in generateStars()" 
           :key="index" 
           :src="require(`@/assets/icon/${star}`)" 
           alt="Star"
           class="star-icon"><br>
            <select v-if="rarityMode==='currentRarity'" v-model="currentRarity" class="rarity-select" :disabled="isImportMode">
                <option v-for="option in rarityOptions" :value="option.value" v-bind:key="option.value">
                    {{ option.text }}
                </option>
            </select><br>
    <img :src="getRoleIcon(character.role)" alt="Role Icon" class="role-icon" />

    <img :src="getElementIcon(character.element)" alt="Element Icon" class="element-icon" />

                </b-col>
        <b-col class="col3">
            <div>
                <span>
                    {{ getGiftName(character.giftL) }}
                    <i class="bi bi-caret-left-fill" :class="getGiftColor(character.giftL)"></i>
                </span>
                <span>
                    <i class="bi bi-caret-right-fill" :class="getGiftColor(character.giftR)"></i>
                    {{ getGiftName(character.giftR) }}
                </span>
            </div>
            <div class="gift-text">{{ character.gift1 }}</div>
            <div class="gift-text">{{ character.gift2 }}</div>
            <div class="gift-text">{{ character.gift3 }}</div>
        </b-col>

    </b-container>
  </template>
  
  <script>
  export default {
    props: {
      character: Object, 
      rarityMode: String,
      isImportMode: Boolean 
    },
    data() {
        return {
            currentRarity: this.character.currentRarity,
            rarityOptions: [
            { text: '1', value: '1' },
            { text: '2', value: '2' },
            { text: '3', value: '3' },
            { text: '3.5', value: '3.5' },
            { text: '4', value: '4' },
            { text: '4.5', value: '4.5' },
            { text: '5', value: '5' }
        ],
        };
    },
    watch: {
        currentRarity(){
            if(!this.isImportMode){
                this.$emit('update-character',  {...this.character, currentRarity: this.currentRarity} );
            }
        }
    },
    methods: {
    generateStars() {
    const  rarity = this.character[this.rarityMode];
    const stars = [];
    for (let i = 1; i <= 5; i++) {
      if (rarity >= i) {
        stars.push('star_fill.webp'); // 星が満たされている場合
      } else if (rarity > i - 1 && rarity < i) {
        stars.push('star_half.webp'); // 星が半分の場合
      } else {
        stars.push('star.webp'); // 星が満たされていない場合
      }
    }
    return stars;
  },
  toggleOwned() {
      if(!this.isImportMode){
        this.$emit('update-character', { ...this.character, owned: !this.character.owned });
      }
    },
      getCharacterIcon(id) {
        return require(`@/assets/character/${id.toString().padStart(3, '0')}.jpg`);
      },
      getRoleIcon(role) {
        return require(`@/assets/icon/${role}.png`);
      },
      getElementIcon(element) {
        return require(`@/assets/icon/${element}.png`);
      },
      getGiftColor(gift) {
        switch (gift) {
          case 'p': return 'text-purple';
          case 'g': return 'text-green';
          case 'r': return 'text-red';
          case 'y': return 'text-yellow';
          case 'b': return 'text-blue';
          default: return '';
        }
      },
      getGiftName(gift) {
        switch (gift) {
          case 'p': return '紫';
          case 'g': return '緑';
          case 'r': return '赤';
          case 'y': return '黄';
          case 'b': return '青';
          default: return '';
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .character-item {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
  }
  
  .character-icon {
    width: 100px;
    height: 100px;
    border-radius: 50%; 
    overflow: hidden;
  }
  .grayscale {
    filter: grayscale(100%);
  }
  
  .character-name {
    font-weight: bold;
  }
  
  .character-title {
    font-size: 0.75em;
  }
  
  .character-rarity {
    margin-top: 5px;
  }
  
  .rarity-select {
    margin-top: 5px;
  }
  
  .role-icon, .element-icon {
    width: 40px;
    height: 40px;
    margin-right: 5px;
  }
  
  .bi {
    font-size: 24px;
    margin-right: 10px;
  }

  
  .text-purple { color: purple; }
  .text-green { color: green; }
  .text-red { color: red; }
  .text-yellow { color: yellow; }
  .text-blue { color: blue; }
  
  .gift-text {
    background-color: rgba(255,255,255,0.2);
    border-radius: 1em;
    padding: 1px;
    font-size: 0.8em;
    margin: 5px 0;
  }
  .col1{
    flex-grow: 2;
    cursor: pointer;
  }
  .col1:hover {
  background-color: rgba(169, 169, 172, 0.5);
  transition-duration: 0.2s;
  transition-property: all;
  transition-timing-function: ease-in;
}
  .col2{
    flex-grow: 2;
  }
  .col3{
    flex-grow: 3;
  }

  .star-icon {
  max-width: 20%;
}
  </style>
  