<template>
  <div
    v-if="visible"
    ref="toastContainer"
    class="position-fixed bottom-0 end-0 p-3"
    style="z-index: 5;"
    aria-live="polite"
    aria-atomic="true"
  >
    <div
      class="toast align-items-center text-white bg-primary border-0"
      role="alert"
      aria-live="assertive"
      aria-atomic="true"
    >
      <div class="d-flex">
        <div class="toast-body">
          {{ message }}
        </div>
        <button
          type="button"
          class="btn-close btn-close-white me-2 m-auto"
          @click="hideToast"
          aria-label="Close"
        ></button>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'bootstrap';

export default {
  name: 'CustomToast',
  props: {
    message: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      visible: false
    };
  },
  methods: {
    showToast() {
      this.visible = true;

      // $refsを使用してToast要素を取得
      this.$nextTick(() => { // DOMが更新された後に実行するために$nextTickを使用
        const toastEl = this.$refs.toastContainer.querySelector('.toast');
        const toast = new Toast(toastEl);
        toast.show();
      });

      // 自動で閉じるためのタイマー
      setTimeout(() => {
        this.hideToast();
      }, 5000); 
    },
    hideToast() {
      this.visible = false;
    }
  }
};
</script>